import React, { useContext } from 'react'
import { Row, Col, Card, Container, Form, Button } from "@themesberg/react-bootstrap"
import { useState, useEffect } from 'react';
import { AuthContext } from '../../../../context/AuthContext';
import { securedAxiosInstance } from '../../../../axiosConfig';
import toast, { Toaster } from "react-hot-toast";
import Loader from "../../../../components/Loader";
import {useHistory} from 'react-router-dom'


function Feedback() {
  const { state } = useContext(AuthContext);
  const [inputState, setInputState] = useState({
    student_id: "",
    email: "",
    name: "",
    roll_no: "",
    branch: "",
    degree: "",
    batch:"",
    feedback_type: "",
    feedback_data: "",
  });
  const history = useHistory();

  const handleChange = (e) => {
    const { id, value } = e.target;
    setInputState((prevState) => ({
      ...prevState,
      [id]: value,
    }));
  }

  const fetchProfileData = async () => {
    securedAxiosInstance
      .get(`/students/me`)
      .then((response) => {
        setInputState({
          student_id: response.data.id,
          name: response.data.Name,
          branch: response.data.branch,
          degree: response.data.degree,
          batch: response.data.batch,
          email: response.data.email,
          roll_no: response.data.rollNumber,
          feedback_type: "query",
          feedback_data: ""
        });
      })
      .catch((error) => {
        console.log(error, error.response);
        toast.error("Something Went Wrong!")
      });
  };

  const uploadFeedbackData = async () => {
    securedAxiosInstance
      .post('/feedbacks', inputState)
      .then((response) => {
        toast.success("Feedback Submitted!")
        setInputState((prevState) => ({
          ...prevState,
          feedback_type: "query",
          feedback_data: ""
        }));
        setTimeout(() => {
          history.push("/my-feedbacks");
        }, 1000);
      })
      .catch((error) => {
        console.log(error, error.response);
        toast.error(error)
      });
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    if (inputState.feedback_data.length === 0) {
      toast.error("Please enter your feedback or query.");
      return;
    }

    uploadFeedbackData();
  }

  useEffect(()=> {
    fetchProfileData()
  }, [state])


  if (inputState.name === "") return <Loader />;

  return (
    <Container className="p-4">
      <Toaster />
      <Card className="p-4">
        <h2 className="text-center mb-4">Feedback Or Query</h2>
          <Form>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group>
                  <Form.Label style={{ fontSize: "1rem" }}>Name :</Form.Label>
                  <Form.Control
                    className="mr-2"
                    required
                    type="text"
                    name="name"
                    value={inputState.name}
                    id="name"
                    placeholder={inputState.name}
                    disabled={true}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group>
                  <Form.Label style={{ fontSize: "1rem" }}>
                    Roll No :
                  </Form.Label>
                  <Form.Control
                    className="mr-2"
                    required
                    type="text"
                    name="roll_no"
                    value={inputState.roll_no}
                    id="roll_no"
                    placeholder={inputState.roll_no}
                    disabled={true}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group>
                  <Form.Label>Email :</Form.Label>
                  <Form.Control
                    required
                    disabled
                    className="mr-2"
                    name="email"
                    value={inputState.email}
                    id="email"
                    type="email"
                    placeholder={inputState.email}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group>
                  <Form.Label>Feedback Type :</Form.Label>
                  <Form.Select
                    value={inputState.feedback_type}
                    id="feedback_type"
                    onChange={handleChange}
                    required
                  >
                    <option value={"query"}>Query</option>
                    <option value={"suggestion"}>Suggestion</option>
                    <option value={"complaint"}>Complaint</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={12} className="mb-3">
                <Form.Group>
                  <Form.Label>Feedback Or Query :</Form.Label>
                  <Form.Control
                    className="mr-2"
                    name="feedback_data"
                    value={inputState.feedback_data}
                    id="feedback_data"
                    as="textarea"
                    rows={5}
                    placeholder="Enter Your Feedback Or Query Here"
                    required
                    onChange={handleChange}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row className="mt-4">
              <Col md={12}>
                <Button variant="dark" type="submit" onClick={handleSubmit}>
                  Submit
                </Button>
              </Col>
            </Row>
          </Form>
        </Card>
    </Container>
  )
}

export default Feedback